import TruffleContract from 'truffle-contract';
import { promisify } from 'util';

import OpenBoxContract from '../contracts/OpenBox.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';

export async function openBoxContract() {
    const contract = TruffleContract(OpenBoxContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function loadGame(issueNo) {
    const contract = await openBoxContract();
    return contract.loadGame(issueNo);
}

export async function betEvent(_from) {
    const contract = await openBoxContract();
    const event = contract.Bet({from: _from}, {fromBlock: 0, toBlock: 'latest'});
    const getAsync = promisify(event.get.bind(event));
    return getAsync();
}
